import { Grid, Typography, styled } from "@mui/material";
import { observer } from "mobx-react-lite";
import React from "react";
import { Link } from "react-router-dom";

const MainPage: React.FC = observer(() => {
    return (
        <Grid container>
            <Grid item xs={8} container justifyContent={'start'}>
                <Typography variant="h4">
                          !
                </Typography>
                <br />
                <Typography variant="subtitle1">
                                .
                        <Link to='/leaders/ranked'> </Link>.       
                             (   ,  
                         ).
                </Typography>
            </Grid>
            <Grid item xs={12} container justifyContent={'start'} alignContent={'start'}>
                <ul>
                    <li><StyledLink to='/leaders/draft'> </StyledLink></li>
                    <li><StyledLink to='/leaders/list'> </StyledLink></li>
                    <li><StyledLink to='/leaders/rank'> </StyledLink></li>
                    <li><StyledLink to='/civs/list'> </StyledLink></li>
                </ul>
            </Grid>
        </Grid>
    )
});

const StyledLink = styled(Link)`
  text-decoration: none;
`


export default MainPage;
